.About {
  height: 100vh;
  background-color: GhostWhite;
  padding: 0;
  margin: 0; }
  .About h2 {
    font-family: 'Yantramanav', sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 11rem;
    margin: 0 0 2rem;
    padding: calc(35vh - 8rem) 2rem 0; }
    @media only screen and (max-width: 735px) {
      .About h2 {
        padding: 2rem 2rem 0;
        font-size: 8rem; } }
  .About .intro {
    position: relative;
    top: -2rem;
    margin: 0;
    padding: 0 2rem;
    font-size: 2rem; }
  .About .techs {
    display: flex;
    flex-flow: row wrap;
    list-style: none;
    padding: 0 1rem; }
    .About .techs li {
      flex: 0 1 auto;
      width: calc(100% / 10 - 2rem);
      min-width: 10rem;
      margin: 1rem;
      padding: 0; }
      .About .techs li svg {
        max-width: 100%;
        height: auto;
        margin: 0;
        font-size: 10rem;
        fill: #333;
        text-anchor: end; }
