.Header {
  position: fixed;
  display: flex;
  width: 100%;
  top: 0;
  padding: 0;
  max-width: 100vw;
  overflow: hidden;
  background-color: #222;
  color: white; }
  .Header span {
    flex: 0 0 auto; }
  .Header form {
    flex: 1 0 auto;
    height: 5rem; }
  .Header .path {
    display: none;
    font-family: 'Space Mono', monospace;
    width: 100%;
    font-size: 3rem;
    line-height: 5rem;
    min-width: 6rem;
    height: 5rem;
    padding: 0;
    background-color: transparent;
    border: none;
    color: White; }
    .Header .path:focus {
      outline: none; }
  .Header .startPath {
    font-family: 'Space Mono', monospace;
    margin: 0 2rem;
    font-size: 3rem;
    line-height: 5rem; }
  .Header h1 {
    font-family: 'Space Mono', monospace;
    flex: 0 1 auto;
    height: 5rem;
    margin: 0 2rem 0 0;
    font-size: 3rem;
    line-height: 5rem;
    font-weight: normal; }
  .Header a {
    padding: 0;
    height: 5rem; }
    .Header a:active, .Header a:focus {
      outline: none; }
  .Header img {
    flex: 0 0 auto;
    height: 5rem; }
  .Header .hidden {
    display: none; }
  @media (min-width: 735px) {
    .Header .path {
      display: unset; } }
